*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

html,
body {
  height: 100%;
  color: #323130;
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

.oui-button {
  border-radius: 4px;
  user-select: none;
  border: 0;
  cursor: pointer;
  display: inline-flex;
  outline: none;
  height: 40px;
  font-size: 14px;
  font-weight: 500;
  padding: 0 16px;
  align-items: center;
  position: relative;
  background-color: #EEE;
  color: #000;
  justify-content: center;
}
.oui-button__icon {
  height: 15px;
  width: 15px;
}
.oui-button__icon--after {
  margin-left: 5px;
}
.oui-button:focus {
  box-shadow: 0 0 0 2px rgba(127, 180, 20, 0.3);
}
.oui-button:not(:disabled):hover {
  background-color: #e9e9e9;
}
.oui-button:not(:disabled):active {
  background-color: #e4e4e4;
}
.oui-button:disabled {
  cursor: not-allowed;
  opacity: 0.7;
}
.oui-button--block {
  display: block;
  width: 100%;
}
.oui-button--large {
  height: 56px;
  font-size: 20px;
}
.oui-button--primary {
  background-color: #7FB414;
  color: #FFF;
}
.oui-button--primary:not(:disabled):hover {
  background-color: #79ab13;
}
.oui-button--primary:not(:disabled):active {
  background-color: #72a212;
}
.oui-button--danger {
  background-color: #ef4d61;
  color: #FFF;
}
.oui-button--danger:not(:disabled):hover {
  background-color: #ee4459;
}
.oui-button--danger:not(:disabled):active {
  background-color: #ed3a50;
}

.oui-button-group {
  display: flex;
  align-items: center;
}
.oui-button-group > .oui-button:not(:last-child) {
  margin-right: 10px;
}

.oui-textarea__label {
  display: block;
  font-size: 14px;
  margin-bottom: 4px;
  font-weight: 600;
}
.oui-textarea__input {
  resize: none;
  border: 1px solid #d0d7de;
  box-shadow: none;
  border-radius: 8px;
  padding: 10px;
}
.oui-textarea__input:focus {
  border-color: transparent;
  outline: #7FB414 solid 2px;
}
.oui-textarea--resizeable .oui-textarea__input {
  resize: both;
}
.oui-textarea--large {
  height: 56px;
  font-size: 20px;
}

.oui-textinput {
  width: 100%;
}
.oui-textinput__label {
  display: block;
  font-size: 14px;
  margin-bottom: 4px;
  font-weight: 600;
}
.oui-textinput__input {
  width: 100%;
  border: 1px solid #d0d7de;
  box-shadow: none;
  border-radius: 4px;
  height: 40px;
  padding: 0 10px;
}
.oui-textinput__input:focus {
  border-color: transparent;
  outline: #7FB414 solid 2px;
}
.oui-textinput--large {
  height: 56px;
  font-size: 20px;
}

.oui-otpinput__description {
  margin-bottom: 10px;
}
.oui-otpinput__digit {
  display: inline-block;
  font-size: 24px;
  text-align: center;
  width: 35px;
  height: 35px;
  border-radius: 4px;
  border: 1px solid #d0d7de;
  margin-right: 10px;
}
.oui-otpinput__digit:focus {
  border-color: transparent;
  outline: #7FB414 solid 2px;
}
.oui-otpinput--large {
  height: 56px;
  font-size: 20px;
}

.oui-input__label {
  display: block;
  font-size: 14px;
  margin-bottom: 4px;
  font-weight: 600;
}
.oui-input__description {
  margin-bottom: 10px;
}

.oui-checkbox {
  display: inline-flex;
  align-items: center;
}
.oui-checkbox__input {
  -webkit-appearance: none;
  -moz-appearance: none;
  opacity: 0;
  width: 0;
  height: 0;
}
.oui-checkbox__input:checked + .oui-checkbox__box {
  background: #7FB414;
  box-shadow: inset 0 0 0 -1px #7FB414;
}
.oui-checkbox__input:checked:disabled + .oui-checkbox__box {
  color: #8f95b2;
}
.oui-checkbox__input + .oui-checkbox__box, .oui-checkbox__input ~ .oui-checkbox__label {
  cursor: pointer;
}
.oui-checkbox__input:disabled + .oui-checkbox__box, .oui-checkbox__input:disabled ~ .oui-checkbox__label {
  cursor: not-allowed !important;
}
.oui-checkbox__input:disabled + .oui-checkbox__box {
  background: #F4F5F9;
  color: #F4F5F9;
  box-shadow: inset 0 0 0 1px #d8dae5;
}
.oui-checkbox__box {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  color: white;
  background: white;
  box-shadow: inset 0 0 0 1px #d8dae5;
  border-radius: 4px;
}
.oui-checkbox__label {
  display: inline-block;
  vertical-align: middle;
  margin-left: 10px;
}

.oui-radio {
  display: flex;
  align-items: center;
}
.oui-radio__input {
  -webkit-appearance: none;
  -moz-appearance: none;
  opacity: 0;
  width: 0;
  height: 0;
}
.oui-radio__input:checked + .oui-radio__circle {
  box-shadow: 0 0 0 1px #7FB414;
}
.oui-radio__input:checked + .oui-radio__circle div {
  background: #7FB414;
  border-color: #7FB414;
}
.oui-radio__input:checked:disabled + .oui-radio__circle div {
  background: #8f95b2;
}
.oui-radio__input + .oui-radio__circle, .oui-radio__input ~ .oui-radio__label {
  cursor: pointer;
}
.oui-radio__input:disabled + .oui-radio__circle, .oui-radio__input:disabled ~ .oui-radio__label {
  cursor: not-allowed !important;
}
.oui-radio__input:disabled + .oui-radio__circle {
  background: #F4F5F9;
  box-shadow: inset 0 0 0 1px #d8dae5;
}
.oui-radio__input:disabled + .oui-radio__circle div {
  background-color: none;
}
.oui-radio__circle {
  width: 24px;
  height: 24px;
  color: white;
  background: white;
  box-shadow: inset 0 0 0 1px #d8dae5;
  border-radius: 50%;
  padding: 5px;
}
.oui-radio__circle div {
  height: 100%;
  width: 100%;
  border-radius: 50%;
}
.oui-radio__label {
  display: inline-block;
  vertical-align: middle;
  margin-left: 10px;
}

.oui-radio-group__label {
  font-weight: 500;
}

.oui-card {
  display: inline-block;
  background: #FFF;
  padding: 20px;
  border-radius: 4px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.12);
}
.oui-card--paddingless {
  padding: 0;
}

.oui-menu {
  padding: 6px 0;
}
.oui-menu__item {
  padding: 6px 16px 0 12px;
  cursor: pointer;
  position: relative;
  height: 36px;
  vertical-align: middle;
}
.oui-menu__item--disabled {
  cursor: not-allowed;
  opacity: 0.5;
}
.oui-menu__item--active {
  background-color: #7FB414;
  color: #FFF;
  font-weight: bold;
}
.oui-menu__item:not(:disabled):not(.oui-menu__item--active):hover {
  background-color: #f6f6f5;
}
.oui-menu__item__name {
  margin-left: 16px;
  vertical-align: middle;
}
.oui-menu__item__icon svg {
  display: inline;
  vertical-align: middle;
  width: 18px;
  height: 18px;
}

.oui-popover {
  position: fixed;
  background-color: white;
  border-radius: 4px;
  box-shadow: rgba(0, 0, 0, 0.13) 0px 3.2px 7.2px 0px, rgba(0, 0, 0, 0.11) 0px 0.6px 1.8px 0px;
}
.oui-popover-trigger {
  display: inline-block;
}

.oui-pagination {
  display: inline-block;
  font-size: 12px;
  font-weight: 500;
  user-select: none;
}
.oui-pagination__list {
  display: flex;
  list-style-type: none;
}
.oui-pagination__item {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 32px;
  width: 32px;
  border-radius: 4px;
  cursor: pointer;
}
.oui-pagination__item:not(:disabled):not(.oui-pagination__item--active):hover {
  background-color: #f6f6f5;
}
.oui-pagination__item--active {
  background-color: #7FB414;
  color: #FFF;
}
.oui-pagination__item--disabled {
  cursor: not-allowed;
  pointer-events: none;
  opacity: 0.6;
}
.oui-pagination__item:not(:last-child) {
  margin-right: 5px;
}

.oui-selectbox {
  display: inline-block;
  position: relative;
}
.oui-selectbox .oui-select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -webkit-font-smoothing: antialiased;
  line-height: 16px;
  cursor: pointer;
  outline: none;
  background: none;
  border: 1px solid #d0d7de;
  border-radius: 4px;
  padding: 10px;
  padding-right: 25px;
  text-overflow: ellipsis;
}
.oui-selectbox .oui-select:focus {
  border-color: transparent;
  box-shadow: 0 0 0 2px #7FB414;
}
.oui-selectbox svg {
  width: 14px;
  height: 14px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 8px;
  pointer-events: none;
}

.oui-switch {
  display: inline-flex;
  cursor: pointer;
  align-items: center;
}
.oui-switch__input {
  -webkit-appearance: none;
  -moz-appearance: none;
  opacity: 0;
  width: 0;
  height: 0;
}
.oui-switch__input:checked + .oui-switch__box {
  background: #7FB414;
  box-shadow: inset 0 0 0 -1px #7FB414;
}
.oui-switch__input:checked + .oui-switch__box .oui-switch__toggle {
  transform: translateX(50%);
}
.oui-switch__input:disabled + .oui-switch__box {
  opacity: 0.5;
}
.oui-switch__input:disabled + .oui-switch__box, .oui-switch__input:disabled ~ .oui-switch__label {
  cursor: not-allowed !important;
}
.oui-switch__box {
  background-color: #d8dae5;
  box-shadow: inset 0 0 0 1px #d8dae5;
  border-radius: 50px;
}
.oui-switch__toggle {
  height: 100%;
  position: relative;
  padding: 2px;
  transition: transform 200ms ease-in-out 0s, transform 200ms ease-in-out 0s;
}
.oui-switch__toggle::after {
  content: "";
  position: absolute;
  height: calc(100% - 4px);
  width: calc(50% - 4px);
  background-color: white;
  border-radius: 50%;
}
.oui-switch__label {
  display: inline-block;
  vertical-align: middle;
  margin-left: 10px;
}

.oui-overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 1;
}

.oui-dialog {
  position: absolute;
  background-color: #FFFFFF;
  left: 50%;
  top: 50%;
  opacity: 0;
  transform: translate(-50%, -50%) scale(0.75);
  transform-origin: 50% 50%;
  border-radius: 8px;
  padding: 24px;
  min-height: 200px;
  min-width: 400px;
  max-width: 600px;
  transition: opacity, transform 0.2s cubic-bezier(0.47, 0, 0.745, 0.715);
}
.oui-dialog--open {
  opacity: 1;
  transform: translate(-50%, -50%) scale(1);
}
.oui-dialog__content {
  padding: 20px 0;
}
.oui-dialog__header {
  display: flex;
  align-items: center;
}
.oui-dialog__close {
  height: 35px;
  width: 35px;
  margin-left: auto;
  cursor: pointer;
  border-radius: 4px;
  display: inline-block;
  padding: 5px;
  color: gray;
}
.oui-dialog__close:hover {
  background-color: #f6f6f5;
}
.oui-dialog__close svg {
  height: 25px;
  width: 25px;
}
.oui-dialog__actions {
  text-align: right;
}

.oui-alert {
  padding: 15px;
  display: flex;
  border-radius: 6px;
  align-items: center;
  margin: 10px 0;
}
.oui-alert--success {
  background-color: #d6f2de;
  color: #3ebe61;
}
.oui-alert--info {
  background-color: #b3d7ff;
  color: #006de6;
}
.oui-alert--danger {
  background-color: #fdeae6;
  color: #eb4e2c;
}
.oui-alert--warning {
  background-color: #ffe5bc;
  color: #ef9400;
}
.oui-alert__title {
  font-weight: bold;
}
.oui-alert__icon {
  height: 26px;
  width: 26px;
  margin-right: 15px;
}
.oui-alert__icon svg {
  fill: currentColor;
}

.oui-table {
  background-color: #FFFFFF;
  width: 100%;
  border-radius: 6px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.12);
  text-align: left;
  border: 0;
  box-sizing: border-box;
}
.oui-table__head .oui-table__cell {
  color: #333;
  border-bottom: 1px solid #EEE;
}
.oui-table__head .oui-table__cell:first-child {
  border-top-left-radius: 6px;
}
.oui-table__head .oui-table__cell:last-child {
  border-top-right-radius: 6px;
}
.oui-table__cell {
  padding: 10px 1rem;
}
.oui-table__cell--checkbox {
  width: 60px;
}
.oui-table__row:not(:last-child) .oui-table__cell {
  border-bottom: 1px solid #EEE;
}
.oui-table__row--clickable {
  cursor: pointer;
}

.oui-table-wrapper {
  background-color: #FFFFFF;
  border-radius: 6px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.12);
}
.oui-table-wrapper__actions {
  padding: 1rem;
  display: flex;
}
.oui-table-wrapper .oui-table {
  box-shadow: none;
}

.oui-segmented-control {
  border-radius: 4px;
  border: 1px solid #CCC;
  margin: 0 10px;
  overflow: hidden;
  display: flex;
  height: 40px;
  text-align: center;
}
.oui-segmented-control .oui-button {
  background-color: transparent;
  border-radius: 0;
  flex-grow: 1;
}
.oui-segmented-control .oui-button:focus {
  box-shadow: none;
}